.slider {
	margin-bottom: 30px;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
}

.image {
	width: 200px;
	height: 200px;
	border-radius: 10px;
}

.right-arrow {
	position: absolute;
	top: 50%-10px;
	right: 10px;
	font-size: 15px;
	color: #000;
	z-index: 10;
	cursor: pointer;
	user-select: none;
}

.left-arrow {
	position: absolute;
	top: 50%-10px;
	left: 10px;
	font-size: 15px;
	color: #000;
	z-index: 10;
	cursor: pointer;
	user-select: none;
}

.slide {
	opacity: 0;
	transition-duration: 1s ease;
}

.slide.active {
	opacity: 1;
	transition-duration: 1s;
	transform: scale(1.08);
}
